/* eslint-disable react-hooks/exhaustive-deps */
import { Suspense, useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import styled from "styled-components";

import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";

import useLyrics from "../../features/lyrics/useLyric";
import { Lyric, TypeVoices } from "../../models/models";
import AppBar from "../../components/AppBar";

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`tabpanel-${index}`}
      aria-labelledby={`tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography component="div">{children}</Typography>
        </Box>
      )}
    </div>
  );
}

function Lyrics() {
  const audioRef = useRef<any>();
  const [tabSelected, setTabSelected] = useState(0);
  const [lyric, setLyric] = useState<Lyric>({} as Lyric);
  const [currentLetter, setCurrentLetter] = useState("");
  const { id } = useParams();
  const { findLyricById, getLetter } = useLyrics();

  const voices = Object.keys(lyric?.voices || []).map((key) => ({
    voice: key as string,
    ...lyric.voices[key as TypeVoices],
  }));

  useEffect(() => {
    try {
      if (id) {
        findLyricById(parseInt(id)).then((data) => {
          if (data) setLyric(data);
        });
      }
    } catch (error) {}
  }, []);

  useEffect(() => {
    if (voices[tabSelected]?.letter) {
      getLetter(
        `${process.env.PUBLIC_URL}/${voices[tabSelected]?.letter}`
      ).then((response) => {
        setCurrentLetter(response.data);
      });
    }
  }, [tabSelected, voices]);

  const handleChange = (_: any, newValue: number) => {
    setTabSelected(newValue);
    updateSong();
  };

  const updateSong = () => {
    if (audioRef.current) {
      audioRef.current.pause();
      audioRef.current.load();
    }
  };

  return (
    <Suspense fallback={<div>Loading...</div>}>
      <AppBar backButton />
      <Container>
        <TopSection>
          <InformationArea>
            <Description>
              <label>{lyric.title}</label>
              <p>{lyric.author}</p>
            </Description>
            <Number>Nº {lyric.number}</Number>
          </InformationArea>
          {voices[tabSelected]?.audio && (
            <AudioArea>
              <audio controls ref={audioRef}>
                <source
                  src={`${process.env.PUBLIC_URL}/${voices[tabSelected].audio}`}
                  type="audio/mpeg"
                />
                Seu navegar não suporta áudio
              </audio>
            </AudioArea>
          )}
        </TopSection>

        <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
          <Tabs
            value={tabSelected}
            onChange={handleChange}
            variant="scrollable"
            aria-label="tabs"
            scrollButtons="auto"
            allowScrollButtonsMobile
          >
            {voices.map((item) => (
              <Tab label={item.voice as string} key={item.voice} />
            ))}
          </Tabs>
        </Box>
        {voices.map((voice, index) => (
          <TabPanel value={tabSelected} index={index} key={voice.voice}>
            <div
              dangerouslySetInnerHTML={{
                __html: currentLetter,
              }}
            ></div>
          </TabPanel>
        ))}
      </Container>
    </Suspense>
  );
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
`;

const Description = styled.div``;

const Number = styled.div`
  font-weight: 500;
  padding: 10px;
  border-radius: 10px;
  border: 1px solid #141514;
  color: #090909;
`;

const TopSection = styled.section`
  display: flex;
  flex-direction: column;

  border-radius: 8px;
  background-color: #4ec30d;
  background-image: linear-gradient(
    140deg,
    #ecf87f 0%,
    #81b622 50%,
    #4ec30d 75%
  );
  padding: 1rem;

  label {
    font-size: x-large;
  }
`;

const InformationArea = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const AudioArea = styled.div`
  margin-top: 1rem;

  audio {
    width: 100%;
  }
`;

export default Lyrics;
