/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useState } from "react";
import styled from "@emotion/styled";
import { useNavigate } from "react-router-dom";

import TextField from "@mui/material/TextField";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import InputAdornment from "@mui/material/InputAdornment";
import SearchIcon from "@mui/icons-material/Search";

import useLyrics from "../../features/lyrics/useLyric";
import AppBar from "../../components/AppBar";

function Home() {
  const [search, setSearch] = useState("");
  const { filteredLyrics, loadLyrics, filterLyrics } = useLyrics();

  const navigate = useNavigate();

  useEffect(() => {
    loadLyrics();
  }, []);

  useEffect(() => {
    filterLyrics(search);
  }, [search]);

  function handleClickItem(id: number) {
    navigate(`/lyrics/${id}`);
  }

  return (
    <>
      <AppBar />
      <Container>
        <Form>
          <TextField
            id="filled-basic"
            label="Buscar"
            variant="filled"
            fullWidth
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <SearchIcon />
                </InputAdornment>
              ),
            }}
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />
        </Form>
        {filteredLyrics.map((item, index) => (
          <LyrycCard
            elevation={16}
            key={index}
            onClick={() => handleClickItem(item.number)}
          >
            <img
              src="https://cdn-icons-png.flaticon.com/512/500/500148.png"
              alt="music icon"
            />
            <LyricDescription padding={1}>
              <Typography variant="h6" textTransform="uppercase">
                {item.title}
              </Typography>
              <Typography variant="body1">{item.author}</Typography>
              <Typography variant="body2">Nº {item.number}</Typography>
            </LyricDescription>
          </LyrycCard>
        ))}
      </Container>
    </>
  );
}

const LyrycCard = styled(Paper)`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin-top: 1rem;
  cursor: pointer;

  img {
    height: 50px;
    width: 50px;

    padding: 10px;
    background-image: linear-gradient(
      140deg,
      #ecf87f 0%,
      #81b622 50%,
      #4ec30d 75%
    );
    border-radius: 10px;
  }
`;

const LyricDescription = styled(Box)`
  display: flex;
  flex-direction: column;
  align-items: end;
`;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  margin: 1rem;
`;

const Form = styled.form`
  display: flex;
  margin-bottom: 1rem;
`;

export default Home;
