import { useState } from "react";
import { useAppDispatch, useAppSelector } from "../../app/hooks";
import LyricService from "../../services/LyricService";
import { setLyrics } from "./lyricSlice";

function useLyrics() {
  const dispatch = useAppDispatch();
  const lyrics = useAppSelector((state) => state.lyrics);
  const [filteredLyrics, setFilteredLyrics] = useState(lyrics.lyrics);

  async function loadLyrics() {
    const lyrics = await LyricService.getAll();
    dispatch(setLyrics(lyrics.data));
  }

  function findLyricById(id: number) {
    return LyricService.findById(id);
  }

  async function filterLyrics(search: string) {
    setFilteredLyrics(await LyricService.search(search));
  }

  async function getLetter(params: string) {
    return await LyricService.getLetter(params);
  }

  return {
    lyricsAll: lyrics.lyrics,
    filteredLyrics,
    loadLyrics,
    findLyricById,
    filterLyrics,
    getLetter,
  };
}

export default useLyrics;
