import axios from "axios";

import { Lyric } from "../models/models";

function getAll(): Promise<{ data: Lyric[] }> {
  return axios.get(`${process.env.PUBLIC_URL}/assets/lyrics/data.json`);
}

async function findById(id: number): Promise<Lyric | undefined> {
  const lyrics = await getAll();
  const found = lyrics.data.find((lyric) => lyric.number === id);
  return found;
}

async function search(search: string): Promise<Lyric[]> {
  const lyrics = await getAll();
  const s = String(search).toLowerCase();
  const filtered = lyrics.data.filter(
    (lyric) =>
      String(lyric.number).includes(s) ||
      String(lyric.author).toLowerCase().includes(s) ||
      String(lyric.title).toLowerCase().includes(s)
  );

  return filtered;
}

async function getLetter(url: string): Promise<{ data: string }> {
  return await axios.get(url);
}

const service = {
  findById,
  getAll,
  search,
  getLetter,
};

export default service;
